import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { FaPhone, FaClock, FaEnvelope, FaCommentDots } from 'react-icons/fa';
import './helpline.css';
import { imageLogo } from '../../asesets';
function MerchantLoginHeader({ loginForm, handleSubmitBack, currentPage }) {
    const [showTooltip, setShowTooltip] = useState(false);
    const navigate = useNavigate();
    const logOut = () => {
        window.localStorage.clear();
        navigate('/login-area');
    };
    return (
        <header className=" page-container ">
            <div className='MerchantLoginHeader'>    <div className="row align-items-center">
                <div className="col-lg-3 col-md-3 col-6">
                    <div className="image-log">
                        <img src={imageLogo} alt="Logo" className="img-fluid" />
                    </div>
                </div>
                <div className="col-lg-3 col-md-4 col-12 mt-2 mt-md-0">
                    <div className="d-flex justify-content-end">
                        {currentPage === '/registrationComplete' ? (
                            <button className="btn btn-outline-danger px-4" onClick={logOut}>
                                LOGOUT
                            </button>
                        ) : (
                            <>
                                <Link to="/login-area" className="btn_custom me-2 px-4">
                                    LOGIN
                                </Link>
                                <Link to="/Signup" className="btn_custom px-4">
                                    SIGNUP
                                </Link>
                            </>
                        )}
                    </div>
                </div>
            </div></div>
        </header>
    );
}

export default MerchantLoginHeader;